.header{
    background-color: hsl(179, 29%, 64%);
    height:30em;
}

.profile-pic{
    border-radius: 50%;
    height: 15em;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-top: 1em;
    margin-bottom: 1em;
}

.name{
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-top: 1em;
    width: 20%;
}

#nameHeader{
    stroke-dasharray: 450;
    stroke-dashoffset: 450;
    animation: sign 5s ease;
    animation-fill-mode:forwards;   
}